.card {
    position: relative;
    width: 200px;
    height: 200px;
    border-radius: 20px;
    box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.3);
    transition: box-shadow 0.5s, opacity 0.5s;
    will-change: transform;
    cursor: pointer;
    overflow: hidden;
    touch-action: none;
    background-size: cover;

  }
  
  .card:hover {
    box-shadow: 0px 30px 100px -10px rgb(31, 31, 37, .5) ;
  }
  
  .card > div {
    will-change: transform;
    height: 100%;
    margin: 0vw 0;
  }
  
  .card > div > * {
    height: 100%;
    background-size: cover;
    background-position: center center;
    margin: 0vw 0;
  }
  
  .container {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
  }
  